	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Unions Short of Labor","date":"01 Sep 2003","slug":"/2003-09-01-unions-short-of-labor/","tags":"Y2003"},"html":"<p>Today is Labor Day in the States, and it is thus somehow appropriate to report on  Germany's second largest and most troubled union <a title=\"IG Metall: Homepage mit Nachrichten\" href=\"http://www.igmetall.de/\">IG Metall</a>, which elected a new chairman in an extraordinary session yesterday. That the traditionalist Jürgen Peters would be elected had already been decided in a smoke-filled room. That he would receive the vote of only 66% of the delegates was a surprise.</p>\n<p><a title=\"almost a diary: Smug for a day.\" href=\"http://tschwarz.freepage4you.de/aad/archives/000931.php\">Tobias</a> and <a title=\"Cum Grano Salis\" href=\"http://cumgranosalis.blogspot.com/2003_08_31_cumgranosalis_archive.html#106235580407051312\">Hans</a>  (trust me on the links, I can't get either to work at the moment) have already commented on the (ir)relevance of unions in  modern German politics. I can only point to the <a title=\"heute.t-online.de - IG Metall leidet unter Mitgliederschwund\" href=\"http://www.heute.t-online.de/ZDFheute/artikel/26/0,1367,WIRT-0-2040026,00.html\">ZDF archive article</a> from April 2003 on declining membership in the IG Metall. Of their 2.64 million members, 8% are youth (students and apprentices?), 24% are pensioners, and 300,000 (6%) are unemployed. That means of that 2.64 million, 38%, or over 1 million, <em>do not work</em>.  (I'm not even counting full-time union and workers' council functionaries...)</p>\n<p>Only 290,000 members are in the eastern states, so it is no surprise that the strike in the east for the 35-hour work week failed so miserably.</p>\n<p>Is it any wonder that when the unions came out against Schröder's reforms, the Chancellor simply laughed in their collective faces?</p>\n<p>Disclaimer: IG Metall also represents workers in the computer software industry. They would be 'my' union if I were inclined to join, and they helped organize the worker's council at my employer.</p>\n<p><b>Update:</b> <a title=\"Lilli Marleen\" href=\"http://www.lillimarleen.blogspot.com/2003_09_01_lillimarleen_archive.html#106245045868991069\">Lilli</a>, an active SPD member, is also somewhat doubtful about Peters.</p>"},"next":{"fields":{"title":"Seeing Rome in One Day?","date":"01 Sep 2003","slug":"/2003-09-01-seeing-rome-in-one-day/"}},"previous":{"fields":{"title":"McDonald?s sets global ad campaign","date":"02 Sep 2003","slug":"/2003-09-02-mcdonalds-sets-global-ad-campaign/"}}},{"node":{"fields":{"title":"Seeing Rome in One Day?","date":"01 Sep 2003","slug":"/2003-09-01-seeing-rome-in-one-day/","tags":"Y2003"},"html":"<p><a title=\" Impossible ... but here's a plan!\" href=\"http://www.twenj.com/romeoneday.htm\">Seeing Rome in One Day?</a></p>"},"next":{"fields":{"title":"Milestones","date":"01 Sep 2003","slug":"/2003-09-01-milestones/"}},"previous":{"fields":{"title":"Unions Short of Labor","date":"01 Sep 2003","slug":"/2003-09-01-unions-short-of-labor/"}}},{"node":{"fields":{"title":"Milestones","date":"01 Sep 2003","slug":"/2003-09-01-milestones/","tags":"Y2003"},"html":"<p>In German <em>Kinder</em> language, the two functions one can perform on the toilet are <em>pi-pi</em> and <em>ah-ah</em>. This weekend Christopher performed <em>ah-ah</em> all by himself, twice. And there was much rejoicing, from both parents and child.</p>\n<p>I guess because we had toilets on our minds, we spontaneously bought new toilet seats for both bathrooms. You can do wild and crazy things like this when you own instead of rent.</p>\n<p>We also took down all the <a href=\"/2001/06/30/1490.php\">safety latches</a> from the kitchen cupboards, since the risk of Christopher randomly emptying the cupboards has decreased to nearly zero. The guard on the stove will stay up for a while, though.</p>\n<p>On Sunday we assembled some cabinets for Oma. Unfortunatey, she selected the cabinets solely on the basis of price, and they were a royal pain to assemble. If you get plain white cabinets from Obi, the DIY chain with the beaver, do NOT get the Simply 'Basic' model. You'll be sorry.</p>\n<p>Afterwards we took Oma and Christopher to the festival at Hof Wörme, an organic farm. Christopher got to ride a horse and a tractor, build a straw puppet, and jump in a hay stack and toss hay in all directions. A good time was had by all, but a bath in the evening was badly needed.</p>"},"next":{"fields":{"title":"Wayward Webloggers Blogging Co-op","date":"29 Aug 2003","slug":"/2003-08-29-wayward-webloggers-blogging-co-op/"}},"previous":{"fields":{"title":"Seeing Rome in One Day?","date":"01 Sep 2003","slug":"/2003-09-01-seeing-rome-in-one-day/"}}}],"pathPrefix":"","first":false,"last":false,"index":640,"pageCount":1101}}

